body {
  margin: 20px;
  padding: 0;
  font-family: sans-serif;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.wrap {
  flex-wrap: wrap;
}

.toolBox {
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
}

.toolBox > h2 {
  font-size: 1.2em;
  margin: 2px 5px;
}

.toolBox > button, .toolBox > a, #saveTraining {
  font-size: 1.2em;
  margin: 0 5px 0 0;
  border: 0px;
  background-color: darkred;
  font-weight: bold;
  color: whitesmoke;
  cursor: pointer;
}

.toolBox > button[disabled] {
  background-color: gray;
  opacity: 0.5;
}

#saveTraining {
  background-color: green;
  color: white;
  font-weight: bold;
  padding: 0.8em;
}

nav {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
  font-size: 1.4em;
}

nav > a {
  display: block;
  text-align: right;
  background-color: gray;
  color: white;
  text-decoration: none;
  margin: 0 5px 0 0;
  padding: 1em 10px 10px 20px;
  width: 6em;
  min-height: 2em;
}

nav > a.active {
  background-color: darkred;
}

nav > a:hover {
  background-color: black;
}

nav.sub {
  font-size: 1.2em; 
}

nav.sub > a {
  padding: 5px;
}

#globalCommands {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  min-height: 2em;
  max-width: 3em;
  
}

#globalCommands button {
  font-size: 1.3em;
  font-weight: bold;
  color: white;
  cursor: pointer;
  background-color: lightgray; 
  margin: 0px 5px 5px 5px;
  padding: 5px;
}

#globalCommands button:hover {
  background-color: darkred;
}

#globalCommands button:active {
  background-color: black;
}

span.tooltiptext {
    visibility: hidden;
    font-weight: 100;
    font-size:1em;
    max-width: 200px;
    background-color: black;
    color: #fff;
    text-align: center;
    padding: 5px;
    border-radius: 6px;
 
    /* Position the tooltip text - see examples below! */
    position: absolute;
    margin: -5px 0px 0px 35px;
    z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
a:hover + span.tooltiptext, span:hover + span.tooltiptext {
    visibility: visible;
}
.typingScreen .userInput {
  font-size: 2em;
  margin-bottom: 10px;
}
.typingScreen .soundButton {
  font-size: 1.5em;
}

.typingScreen form {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin-bottom: 20px;
}

.soundControls {
  display: flex;
  align-items: center;
}

.soundControls > * {
  margin-right: 10px;
}
.morseWord {
    font-size: 2em;
    font-family: Consolas;
    display: flex;
    flex-direction: row;
}

.morseToken {
    background: lightgray;
    margin: 0 10px 10px 0;
    display: flex;
    flex-direction: column;
    min-width: 3.4em;
    text-transform: capitalize;
    box-shadow: 4px 4px 10px;
}
.learningScope {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.learningScope > button {
  font-family: consolas;
  font-size: 1.2em;
  display: block;
  margin-right: 15px;
  margin-bottom: 15px;
  cursor: pointer;
  background: lightgray;
  padding: 5px;
}

.inScope {
  background: green !important;
  color: white;
}

.numberWithUnit {
  display: flex;
  flex-direction: column;
  background-color: lightblue;
  margin-right: 5px;
}

.numberWithUnit span:first-of-type {
  font-size: 2em;
  font-weight: bold;
  margin-left: 5px;
  margin-right: 20px;
}

.numberWithUnit span:nth-of-type(2) {
  text-align: right;
  font-size: 0.8em;
  margin: 2px 5px;
}

#results span {
  width: 20px;
  height: 30px;
  margin-right: 5px;
}

#results .success {
  background-color: green;
}

#results .fail {
  background-color: red;
}

#training > div {
  display: flex;
  flex-direction: row;
}

#training p {
  font-size: 3em;
  background: lightgray;
  padding: 0.5em;
  margin-right: 10px;
}
.tableData {
  margin-right: 40px;
}

.tableData h3 {
  font-size: 1.2em;
}

.tableData table {
  border-spacing: 0px;
}

.tableData th {
  text-align: left;
  padding: 5px;
  padding-right: 10px;
  font-size: 1.2em;
  font-weight: bold;
  border-bottom: 2px solid #111;
  color: #111;
}

.tableData td {
  text-align: left;
  padding: 5px;
  padding-right: 10px;
  border-bottom: 1px solid #333;
  color: #111;
}

.tableData tbody:before {
    content: "-";
    display: block;
    line-height: 0.5em;
    color: transparent;
}

.tableData tbody tr:hover {
  background-color: lightgray;
}

.tableData tr > td {
  text-align: right;
}
